<!-- 搜索页 -->
<template>
  <div class="search">
    <SearchBar @searchVal="searchVal" ></SearchBar>
    <div class="content">
      <div class="search-list" v-show="inputVal.length > 0">
        <div class="loading" v-show="isLoadingShow">
          <img src="../../assets/images/loading.gif" alt />
          <p>搜索中...</p>
        </div>
        <div class="empty" v-show="isEmptyShow && searchViewResultList.length === 0">搜索结果为空</div>
        <div class="search-result" v-show="!isLoadingShow">
          <div class="view" v-show="searchViewResultList.length">
            <h5><i />视图公号</h5>
            <ul>
              <li v-for="(item, index) in searchViewResultList" :key="index" @click="toChat(item.moduleid, item.modulename, item.icon)">
                <span class="left">
                  <span class="text" v-html="item.text"></span>
                  <img :src="item.icon" alt="">
                </span>
              </li>
            </ul>
          </div>
          <div class="article" v-show="chatList.length">
            <h5><i />近3月消息记录</h5>
            <ul>
              <li v-for="(item, index) in chatList" :key="index" @click="toChat(item.moduleid, item.modulename, item.icon)">
                <span class="left">
                  <span class="text" v-html="item.text"></span>
                  <img :src="item.icon" alt="">
                </span>
                <span class="auth">
                  {{item.count}}条相关聊天记录
                </span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
import SearchBar from "../../components/SearchBar";
import axios from 'axios';
const productionUrl = window.origin + '/mobile456/xxtx/py';
const instance = axios.create({
  baseURL: process.env.NODE_ENV === 'development' ? '/api/mobile' : productionUrl
});
export default {
  name: "Search",
  components: {
    SearchBar
  },
  data() {
    return {
      inputVal: "",
      applyVal: '',
      isLoadingShow: false,
      isEmptyShow: false,
      isShowMore: false,
      searchViewResultList: [],
      chatList: [],
    };
  },
  created() {},
  methods: {
    // 获取搜索框值
    searchVal(val) {
      this.inputVal = val;
      this.searchViewResultList = [];
      this.chatList = [];
      if (this.inputVal) {
        this.isLoadingShow = true;
        this.isEmptyShow = false;
        const reg = new RegExp(this.inputVal, "g");
        instance.get('/search', {
          params: {
            "kw": this.inputVal,
            "aamid": this.$store.state.userInfoObj.aamid,
            // "aamid": "100"
          }
        }).then(res => {
          this.isLoadingShow = false;
        	if (res.status === 200 && res.data.message === 'OK') {
            const { chat = [], module: viewList = [] } = res.data.data || {};
            if (chat.length === 0 && viewList.length === 0) {
              this.isEmptyShow = true;
              return;
            }
            // // 关键字高亮
            this.searchViewResultList = viewList.map(i => {
              return {
                text: i.modulename.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                ),
                ...i
              };
            });
            this.chatList = chat.map(i => {
              return {
                text: i.modulename.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                ),
                ...i
              };
            });
        	} else {
        		this.isEmptyShow = true;
        	}
        }).catch(err => {
        	this.isLoadingShow = false;
        	this.isEmptyShow = true;
        });
      }
    },
    // 点击展示更多
    showMore() {
      this.isShowMore = !this.isShowMore;
    },
    // 跳转到详情
    toChat(moduleid, modulename, icon) {
      const aamid = this.$store.state.userInfoObj.aamid;
      this.$router.push({name: 'MessageChat', params: { moduleid, modulename, icon, kw: this.inputVal }});
      // window.location.href = `${productionUrl}/xxtx/py/chat?moduleid=${moduleid}&modulename=${modulename}&moduleIcon=${icon.split('//')[1]}&aamid=${aamid}&kw=${this.inputVal}`;
    },
  }
};
</script>
<style lang="less" scoped>
.search {
  min-height: 100%;
  background: #fff;
}
.search-btn{
  width: 0.52rem;
  height: 0.3rem;
  background: #45A4B1;
  border-radius: 0.15rem;
  color: #fff;
  text-align: center;
  line-height: 0.3rem;
  font-size: 0.15rem;
  margin-left: 0.06rem;
}
.content {
  .search-list {
    .loading {
      padding-top: 1.2rem;
      text-align: center;
      font-size: 0.14rem;
      color: #666666;
      img {
        width: 0.4rem;
      }
    }
    .empty {
      padding-top: 1.2rem;
      text-align: center;
      font-size: 0.14rem;
      color: #666666;
    }
    li {
      height: 0.44rem;
      line-height: 0.44rem;
      font-size: 0.15rem;
      color: #232323;
      border-bottom: 1px solid #eaeaea;
      box-sizing: border-box;
      padding: 0 0.1rem 0 0.17rem;
      display: flex;
      align-items: center;
      position: relative;
      overflow: hidden;
      .left{
        flex: 1;
        display: flex;
        align-items: center;
        overflow: hidden;
        height: 0.44rem;
        .text{
          height: 0.44rem;
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
          // max-width: calc(100% - 0.6rem);
          padding-right: 0.09rem;
          box-sizing: border-box;
        }
      }
      img{
        width: 0.3rem;
        height: 0.3rem;
        margin-left: 0.1rem;
      }
      .auth{
        float: right;
        font-size: 0.12rem;
        color: #969696;
      }
    }
  }
  .search-result{
    h5{
      height: 0.4rem;
      font-size: 0.14rem;
      color: #969696;
      line-height: 0.4rem;
      position: relative;
      text-indent: 0.16rem;
      border-bottom: 1px solid #EAEAEA;
      i{
        position: absolute;
        width: 0.04rem;
        height: 0.24rem;
        background: #2A7875;
        left: 0;
        top: 0.08rem;
      }
    }
  }
  .more{
    height: 0.16rem;
    padding-top: 0.1rem;
    font-size: 0.11rem;
    color: #45A4B1;
    display: flex;
    justify-content: center;
    align-items: center;
    img{
      width: 0.12rem;
      height: 0.13rem;
      margin-left: 0.02rem;
    }
    .roate{
      transform: rotate(180deg);
    }
  }
}
</style>